import {base_url} from './config.js'

export const apiResquest = (prams) => { //prams API 

	// 
	let headerData = {
		"content-type": "application/x-www-form-urlencoded" //
	}

	let dataObj = null
        //GETPOST
	if (prams.method === "GET") {
		headerData = {
			'content-type': 'application/json',
			// 'token': uni.getStorageSync('token')
		}
	} else if(prams.method === "POST"){
		headerData = {
			"content-type": "application/x-www-form-urlencoded" //
			// 'token': uni.getStorageSync('token')
		}
	}
	// else {
	// 	dataObj = {
	// 		'data': prams.data,
	// 		'token': uni.getStorageSync('token')
	// 	}
	// }
	return new Promise((resolve, reject) => {
		let url = base_url + prams.url; //api
		uni.showLoading({
			title: '',
			mask: true
		})
		return uni.request({
			url: url,
			data: prams.data,
			method: prams.method,
			header: headerData,
			success: (res) => {
				uni.hideLoading()
                //
				if (res.data.code !== '0000') {
					uni.showToast({
						title: ':' + res.data,
						duration: 1000,
						icon: "none"
					})
					return;
				}
				resolve(res.data);
			},
			fail: (err) => {
				reject(err);
				console.log(err,"errorrrrrrrrr")
				uni.hideLoading()
			},
			complete: () => {
				console.log('')
				uni.hideLoading()
			}
		});
	})
}