import {apiResquest} from './http.js'

/**
 * 
 * 
|      |    |                                |
| -------- | ------ | ---------------------------------- |
| code     | String | wx.logincode           |
| userInfo | Object | getUserProfileuserInfo |
 */
export const login = (code,userInfo) => {
	return apiResquest({
		url: '/login',
		data:{
			code:code,
			userInfo:userInfo
		},
		method: 'POST',
	})
}

